
/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
.header{
	width:100%; 	
	order: 1;	
    -webkit-order: 1;
    position:fixed;       
    left: 0;  right:0;         
    z-index: 50;     
    top:0; width:100%;  
    height:70px; transition: all 0.3s linear; padding:20px 0 16px;
    font-family: $title-font-family;
    .wrapper {padding: 0 20px;}
    &:before {transition: all 0.3s linear;content:'';position: absolute;z-index:0;left:0;right:0;top:0;background: linear-gradient(169.3deg, #3A5374 0%, #262D42 99.41%);height: 0%}
    &.fix { 
        &:before {height: 100%;}
    }   

}   

.hid {overflow: hidden;}
.header_right {float:right; display: flex; align-items: center; justify-content: flex-start;}
.header_logo {overflow: hidden; float:left;width:165px;height:40px;line-height: 0; position: relative;z-index: 30;transition: all 0s linear;
    img {height:100%;max-width: 9999px;}
}      
.header_btn{
    .btn {width:120px; display:block;height: 30px; padding:0; line-height:28px; font-weight: 600;font-size: 13.5px; }
}
.header_login_link {    font-size: 13.5px;font-weight: 600; text-transform: uppercase;color: #FFFFFF;
    &:hover {color: #0B9BD0;} 
}

/* */
@media screen and (max-width: 767px) {       
    .header_right {flex-direction: row-reverse;}
    .header_logo {height: 40px; width: 46px;    margin: -4px 0 0 2px;}  
    .header_login {margin-left: 20px;}
}   
